import React from 'react';
import { Route, Switch } from 'react-router-dom';

import Loadable from 'react-loadable';

import Navigation from './Navigation';

import Loading from './Loading';
const asyncHome = Loadable({
  loader: () => import(/* webpackChunkName: 'Home' */ './pages/Home'),
  loading: Loading,
});
const asyncAbout = Loadable({
  loader: () => import(/* webpackChunkName: 'About' */ './pages/About'),
  loading: Loading,
});
const asyncAlonePage = Loadable({
  loader: () =>
    import(/* webpackChunkName: 'Alone-Page' */ './pages/AlonePage'),
  loading: Loading,
});
const asyncColections = Loadable({
  loader: () =>
    import(/* webpackChunkName: 'Colections' */ './pages/Colections'),
  loading: Loading,
});
const asyncPageNotFound = Loadable({
  loader: () => import(/* webpackChunkName: 'Home' */ './pages/PageNotFound'),
  loading: Loading,
});

const boxStyle = {
  border: '1px solid green',
  width: '800px',
  margin: '0 auto',
  height: '100vh',
};

const App = () => (
  <>
    <Navigation />
    <div style={boxStyle}>
      <Switch>
        <Route exact path="/" component={asyncHome} />
        <Route path="/about" component={asyncAbout} />
        <Route path="/colections/:name" component={asyncAlonePage} />
        <Route path="/colections" component={asyncColections} />
        <Route component={asyncPageNotFound} />
      </Switch>
    </div>
  </>
);

export default App;
